<template>
 <div class="plekan-translate-mode-column">
    <div :class="{empty: !rows.length,translate:isTranslate}" class="plekan-row-list" id="plekan-sortable-list">
      <div class="plekan-row-item animated fadeInLeft transition-component" v-for="(r,key) in rows" :key="r.index">
        <div class="plekan-tools">
          <span v-if="!isTranslate" class="plekan-move-row"><i class="fa fa-hand-grab-o"></i></span>
          <span @click="editAsHTMLRow(r,key,language)"><i class="fa fa-html5"></i></span>
          <span  v-if="!isTranslate" @click="deleteRow(r,key)"><i class="fa fa-remove"></i></span>
          <span  v-if="!isTranslate" @click="dublicateRow(r,key)"><i class="fa fa-copy"></i></span>
        </div>
        <component  
                    :is="r.name" 
                    :index="key"
                    :store="store" 
                    :displayLanguage="language">
        </component>
      </div>
    </div>
  </div> 
</template>

<script>

  /**
   * GELTRC NOTU
   *
   * <component> component'i vue tarafndan salanr. Bu komponent'ler mixin
   * ile hazrland iin alt taraftaki zellikler bu komponen'te gnderilir
   * Mixin.js core/mixin.js
   *
   * "is"              -> component ismi /src/core/modules/list.json
   * "index"           -> benzersiz kimilii (mixin tarafndan oluturulur)
   * "store"           -> mixin global store'dan izoledir. Bu yzden mixin'e global store pass edilir  /src/store/index.js
   * "displayLanguage" -> hangi dilde grntlenecei  https://github.com/abdullah/plekan#row-objesi
   * "isTranslate"     -> translate mod'a getiinde baz butonlarn gzkmemesi iin.
   */
  import store from 'plekan/store'

  export default {
    /** @type {Array} Arena.vue tarafndan ynetilir. */
    props:["rows","language","editAsHTMLRow","isTranslate"],
    data () {
      return {
        /** @type {Object} Global store */
        store: store
      }
    },
    methods:{
      /**
       * Row silmek istendiinde tetiklenir
       * @private
       * @param  {Object} row
       * @param  {Number} index
       * @return {void}
       */
      deleteRow(row,index){
        this.store.deleteRow(row,index)
      },
       /**
        * Row kopyalanmak istendiinde tetiklenir
        * @private
        * @param  {Object} row
        * @param  {Number} index
        * @return {void}
       */
      dublicateRow(row,index){
        this.store.dublicateRow(row,index)
      },
    }
  }
</script>

<style>
  
</style>



