<template>
  <div class="plekan-area">
    <!-- This components for Preview and Translate language change  -->
    <change-language></change-language>
    <!-- Arena Container  -->
    <div :class="{'plekan-translate-mode' : translateMode}" class="plekan-container">
      <!-- Arena Column - Preview  -->
      <arena-column :isTranslate="false" :rows="rows" :editAsHTMLRow="editAsHTMLRow" :language="currentLanguge"></arena-column>
      <!-- Arena Column - Translate  -->
      <arena-column :isTranslate="true" :rows="rows" :editAsHTMLRow="editAsHTMLRow"  :language="translateLanguage" v-show="translateMode"></arena-column>
      <!-- Edit As HTML Modal  -->
      <modal :show="editRow.row ? true : false" class="edit-modal">
        <header slot="header">
          <div class="plekan-modal-title">Edit As Html</div>
        </header>
        <div slot="body" class="plekan-edit-as-html-modal-body">
          <textarea v-model="editRow.html"></textarea>
        </div>
        <footer slot="footer" class="plekan-clearfix">
          <button type="button" @click.prevent="saveEditAsHtml">Save HTML</button>
        </footer>
      </modal>
    </div>
    <!-- Arena Footer -->
    <div class="plekan-container">
    <!-- <pre>{{store.state.rows}}</pre> -->
      <div class="plekan-footer">
        <button v-for="b in $plekan_buttons"
                type="button" 
                :class="b.class"
                @click="b.callback(returnStoreRows)">
                {{b.text}}
        </button>
      </div>
    </div>
  </div>
</template>
<script>
  import store from 'plekan/store'
  import modal from 'plekan/components/modal'
  import Sortable from 'sortablejs'
  import {arenaSortableOptions} from 'plekan/core/sortable_options'
  import changeLanguage from 'plekan/components/changeLanguage'
  import arenaColumn from 'plekan/components/arenaColumn'

  /**
   * NOTE FOR DEVELOPERS *****
   *
   * You can understand this section once you use pelikan.
   *
   * This component is a body for application. This component contains;
   * translate area , preview area , which language choose for displaying by
   * user.
   *
   * For example: Modal is used to edit component as HTML.
   */
  export default {
    props:[],
    data () {
      return {
        // @type {Object} Each component, use and access the global store.
        store : store,
        // @type {Object} The object consists of language,html,index and row properties.
        editRow : {
          lang: null,
          html: null,
          index: null,
          row: null,
        }
      }
    },
    /**
     * Components are used in the area
     * @todo If you don't have any idea of components keyword:
     * https://vuejs.org/guide/components.html 
     */
    components: {
      modal,
      changeLanguage,
      arenaColumn
    },
    computed: {
      /**
       * Clean observable object
       * @return {Array} Store rows
       */
      returnStoreRows(){
        return JSON.parse(JSON.stringify(this.store.state.rows))
      },
      /**
       * This method return a boolean value for split arena of which preview
       * language
       *
       * Hesaplanan deer ekran iki veya tek ekilde gstermek iin
       * kullanlr Bu durum arena'nn bo olmas durumdaki grntyde
       * etkiler change-language komponent'i tarafndan deitirilir
       * @private
       * @return {Boolean}
       */
      translateMode: function () {
        return this.store.state.translateMode;
      },
      /**
       * Global store'da tutulan ve ekranda gsterilecek her bir row'un
       * bulunduu diziyi hesaplar.
       * @private
       * @return {Array}  https://github.com/abdullah/plekan#row-objesi
       */
      rows : function () {
        return this.store.state.rows
      },
      /**
       * currentLanguge hesaplamas change-language iindeki select'lere bal
       * alr ekranda gsterilecek row'larn hangi dilde olacan hesaplar
       * . plekan.js dosyasnda dardan alnn seeneklere gre ilk ( init )
       * zamannda belirlenir ve sonradan deitirilebilirdir
       * https://github.com/abdullah/plekan#seenekler
       * @private
       * @return {String} 
       */
      currentLanguge: function () {
        return this.store.state.currentLanguge
      },
      /**
       * currentLanguge ile ayn zelliklere sahiptir. Ekran ikiye
       * blndnde translate olacak ksmn hangi dilde olmas gerektiinin
       * hesaplar bu translateMode'un aktif (true) olduunu gsterir
       * @private
       * @return {String} 
       */
      translateLanguage: function () {
        return this.store.state.translateLanguage
      },
      /**
       * Eklentiye tantlm her bir row'un retilecei komponent'leri temsil
       * eder.zel olarak yazlm veya daha nceden eklentinin ekirdeinde
       * yer alan modl listeleridir. plekan.js iinde init zamannda global
       * store kayt edilir.
       * @private
       * @return {Object}  /src/core/modules/list.json
       */
      list : function () {
        return this.store.state.moduleList
      }
    },
    mounted(){
      /*
       * Sortable options init , see: https://github.com/RubaXa/Sortable
      */
      let el = document.getElementById("plekan-sortable-list");
      Sortable.create(el,{
        ...arenaSortableOptions,
        onAdd:this.onAdd,
        onEnd:this.onEnd,
      });
      /*
       * Modal komponenti kapatlmak istendiinde tetiklenir. Kapatma istei
       * "kapatma" ikonuna veya modal dna tklandnda tetiklenir Bu
       * olaydinlemesi gvenli k olarak kullanlabilir.
       * @TODO : Move function in requestHiddenModal from to  new function 
      */
       document.addEventListener('requestHiddenModal', (e) => {
            if (this.editRow.row) {
              /*1*/
              Object.keys(this.editRow).map(e => this.editRow[e] = null)
            }
        },false);
    },
    methods:{
      /**
       * Bu method arena-column komponent'i ierisinden arlr. Bu method
       * arena-column komponentine zellik olarak geilir (pass edilir).
       *
       * Bu method'a gelen deerler local scope'daki "editRow" objesinde
       * saklanr.
       *
       * Temel mantk iin arena-column'a bakn.
       * @param  {Object} row      Dzenlenecek row'un kendisi
       * @param  {Number} index    Store objesinin iinde hangi index'te yer
       * ald
       * @param  {String} language Hangi dil iin dzenleme yaplaca
       * @return {void} 
       */
      editAsHTMLRow(row,index,language){
        this.editRow.row    = JSON.parse(JSON.stringify(row))
        this.editRow.lang   = language
        this.editRow.index  = index
        this.editRow.html   = this.editRow.row.contents[language].html

      },
      /**
       * editRow objesi store'daki asl obje ile deitirilir editRow objesi
       * ilem bittikten sonra bolatlr
       * @return {void} 
       */
      saveEditAsHtml(){
        this.editRow.row.contents[this.editRow.lang].html = this.editRow.html
        this.store.updateRows(this.editRow.index,this.editRow.row)
        /*1*/
        Object.keys(this.editRow).map(e => this.editRow[e] = null)
      },
      /**
       * see: https://github.com/RubaXa/Sortable
       * @param  {Object of Sortable Event} evt
       * @return {void}
       * @todo Global event atanacak.
       * https://github.com/abdullah/plekan#olay-yakalama
       *
       * Sortable eklentesi tarafndan srkleme bittiinde srklenen liste
       * elemann bir kopyasn oluturur.
       * @todo daha iyi anlamak iin fonksiyonu kaldrn. 
       */
      onAdd(evt){
        evt.item.remove();
      },
      /**
       * Row'larn yer deitirmesinden sonra bu event tetiklenir
       * Yeni ve eski yerlerine gre store gncellenir
       * @param  {Object of Sortable Event} evt
       * @return {void}
       */
      onEnd(evt){
          this.store.sortRows(evt.newIndex,evt.oldIndex)
      }
    }
  }
</script>


