<template>
     <div class="plekan-change-language plekan-clearfix">
      <div class="plekan-container">
        <div class="plekan-pull-left">
          <span>Preview Language</span>
          <select  v-model="store.state.currentLanguge" class="plekan-form-control">
            <option v-for="l in languages" :value="l">{{l}}</option>
          </select>
        </div>
        <div class="plekan-open-translate-arena">
          <button @click="store.changeTranslateMode()"><i class="fa fa-language"></i></button>
        </div>
        <div class="plekan-pull-right" v-show="translateMode">
          <span>Translate Language</span>
          <select  v-model="store.state.translateLanguage" class="plekan-form-control">
            <option v-for="l in languages" :value="l">{{l}}</option>
          </select>
        </div>
      </div> 
    </div> 
</template>

<script>
  import store from 'plekan/store'

  export default {
    props:[],
    data () {
      return {
        store:store
      }
    },
    computed:{
      /**
       * Global store'daki dil koleksiyonunu dndrr
       * Dil koleksiyonundaki diller kadar seenek sunar.
       * @return {Array} 
       */
      languages : function () {
        return this.store.state.languages
      },
       /**
        * Global store'daki eviri modunun aktif olup olmadn dndrr.
        * Burdan dnen deere gre local template'te deiiklil yaplr
        * rnek : v-show="translateMode"
        * @return {Boolean} 
       */
      translateMode:function () {
          return this.store.state.translateMode;
      },
    }
  }
</script>

<style>
  
</style>



