<template>
   <modal :show="shown">
      <header slot="header">
        <div class="plekan-modal-title">File Upload</div>
      </header>
      <div slot="body" class="plekan-modal-color-body plekan-clearfix">
        <file-upload
        :clean="shown"
        :types="$allowedFileTypes"
        :fileChange="fileChange"></file-upload>

      </div>
      <footer slot="footer" class="plekan-clearfix">
        <button 
          class="plekan-footer-button" @click="onFileUpload" :disabled="!file">Upload</button>
      </footer>
    </modal>
</template>

<script>
  import modal from 'plekan/components/modal'
  import fileUpload from 'plekan/components/fileUpload'
  import {exec} from 'plekan/helper'

  export default {
    props:["shown","close"],
    data () {
      return {
        file:null
      }
    },
    components: {
      modal,
      fileUpload
    },
    methods:{
      /**
       * Bu method file-upload componentine property olarak pass edilir.
       * file-upload'da geri dnen deer file objesi local scope'a alnr
       *
       * Daha fazlas iin file-upload.vue'ye baknz
       * @param  {Object of File} file
       * @return {void}
     */
      fileChange(file){
        this.file = file 
      },
       /**
       * Upload button'nuna tklandnda bu method arlr.
       *
       * Callback method'unden geri gelen deer ile link oluturulur
       *
       * this.$onFileUpload fonksiyonu global'dir. Plekan.js
       * #Vue.prototype.$onFileUpload tanmalasna baknz
       * @return {void} 
      */
       onFileUpload(){
        /*
        @TODO : Pass file
        */
        this.$plekanEvent.onFileUpload(this.file, (url) => {

          exec('insertHTML', `<a href="${url.src}" target="_blank">${url.title || url.src}</a>` );
          
          this.close()

        })

      },
    }
  }
</script>

<style>
  
</style>



